import { Component, OnInit } from '@angular/core';
import { EnrollService } from '../enroll.service';


@Component({
  selector: 'app-our-form',
  templateUrl: './our-form.component.html',
  styles: []
})
export class OurFormComponent implements OnInit {

  submitted = false;
  
  constructor(private enrollServ: EnrollService) { }
  public id: string; //Our is columns in the db
  public firstname: string;
  public lastname: string;
  public address: string;
  public city: string;
  public state: string;
  public zip: string;
  public phone: string;
  public email: string;
  public dos: string

  ngOnInit() {
  }

  onSubmit(form) { //form reaches here on submit
    this.submitted = true;
    this.enrollServ.enroll(form.value)
    .subscribe(
      data => console.log('Sucess', data),
      error => console.log('Error', error)
    );
  }

}
