//Injection of Service. 

import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Student } from './students';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EnrollService {

  _URL = 'https://9fjpezmhcj.execute-api.us-east-1.amazonaws.com/student/studentform/enroll';

  constructor(private _http: HttpClient) { }

  enroll(stud: Student) {
    return this._http.post<any>(this._URL, stud, {headers: new HttpHeaders().set('Content-Type', 'application/json')});
  }
}
